<template>
  <!-- WARNING: prevent side effect. (neighbor window affected when before component resized) -->
  <!-- WARNING: even rendering time is different(chrome and firefox). set 0px, 0px won't render in chrome -->
  <div class="tg-window__root" style="width: 1px; height: 1px">
    <div class="tg-window__stem">
      <!-- header -->
      <window-header :title="title" @onMinimize="minimize"></window-header>
      <!-- body -->
      <transition name="slide-fade">
        <div class="truegaze-window__body elevation-24" v-show="!flags.collapse" style="position: relative">
          <span>
            <div v-if="this.flags.showConfirmDialog" class="tg-window__confirm-dialog justify-center align-center">
              <div>
                <p>{{ confirmDialogMsg }}</p>
                <tg-button class="confirm" @click="onClickConfirm">Confirm</tg-button>
                <tg-button @click="flags.showConfirmDialog = false">Deny</tg-button>
              </div>
            </div>
            <div class="tg-window__focus-curtain"></div>
            <slot></slot>
          </span>
        </div>
      </transition>
      <!-- <window-loader v-if="options.useLoader"></window-loader> -->
    </div>
  </div>
</template>

<script>
import theme from '../class/Theme';

import WindowHeader from './WindowHeader.vue';
import { makeResizable, makeDraggable } from '../utils/make-interactable';
import { setWidth, setHeight, setStyle } from '../utils/dom-helper';
import { defaultSizes } from '../constatns/settings';

export default {
  components: {
    WindowHeader,
  },
  props: {
    windowEventBus: Object,
    terminateAction: Function, // TODO: not yet impl
    windowOptions: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },
  data: function () {
    return {
      beforeMinimizeHeight: undefined,
      flags: {
        collapse: false,
        showConfirmDialog: false,
      },
      options: {
        minimizable: false,
      },
      title: undefined,
      confirmDialogMsg: undefined,
    };
  },
  mounted() {
    this.bindComponent();

    setStyle(this.$el.querySelector('.truegaze-window__body'), { background: theme.getWindowBodyColor() });
  },
  methods: {
    bindComponent(windowOptions) {
      const { labels = {} } = this.windowOptions;
      this.title = labels.title;

      // parse component attribute
      const { mode = 'static', draggable = true, minimizable = false, initSize = {}, minSize = {} } =
        this.windowOptions || {};

      const stemEl = this.$el.querySelector('.tg-window__stem');
      const headerEl = this.$el.querySelector('.tg-window__header');
      const bodyEl = this.$el.querySelector('.truegaze-window__body');

      if (mode === 'static') {
        // not yet impl
      } else if (mode === 'fit-content') {
        bodyEl.style.width = 'fit-content';
        bodyEl.style.height = 'fit-content';
      } else if (mode === 'resizable') {
        const initWidth = initSize.w || defaultSizes.initSize.w;
        const initHeight = initSize.h || defaultSizes.initSize.h;
        initWidthHeight(stemEl, { initWidth, initHeight });

        const minWidth = minSize.w || defaultSizes.minSize.w;
        const minHeight = minSize.h || defaultSizes.minSize.h;
        makeResizable(bodyEl, stemEl, { minWidth, minHeight });
      } else {
        throw new Error(`You should declare 'mode' property for create window. mode=[${mode}]`);
      }

      if (draggable) {
        makeDraggable(headerEl, stemEl);
      }

      if (minimizable === false) {
        this.options.minimizable = false;
      } else {
        this.options.minimizable = true;
      }
    },
    enableCurtain(flag) {
      const el = this.$el.querySelector('.tg-window__focus-curtain');
      if (flag) {
        el.style.display = 'block';
      } else {
        el.style.display = 'none';
      }
    },
    setFocus(flag) {
      const el = this.$el.querySelector('.tg-window__focus-curtain');
      if (flag) {
        el.style.display = 'none';
      } else {
        el.style.display = 'block';
      }
    },
    toggle() {
      this.flags.collapse = !this.flags.collapse;
    },
    minimize($event) {
      this.$emit('minimize', this.title);
    },
    // maximize() {
    //   this.flags.collapse = false;

    //   // pop current height
    //   if (this.beforeMinimizeHeight) {
    //     const rootEl = this.$el.querySelector('.tg-window__stem');
    //     rootEl.style.height = this.beforeMinimizeHeight;
    //   }
    // },
    onNotifyInfo(msg) {
      this.infoMsg = msg;

      setTimeout(() => {
        this.infoMsg = undefined;
      }, 1000);
    },
    onNotifyConfirm(msg, successCallback) {
      // show dialog
      this.flags.showConfirmDialog = true;
      this.confirmDialogMsg = msg;
      this.$options.confirmCallback = successCallback;
    },
    onClickConfirm() {
      const callback = this.$options.confirmCallback;
      this.flags.showConfirmDialog = false;
      callback();
    },
  },
};

function initWidthHeight(rootEl, { initWidth, initHeight }) {
  setWidth(rootEl, initWidth || defaultSizes.width);
  setHeight(rootEl, initHeight || defaultSizes.height);
}
</script>

